<template>
    <nav class="cate">
        <span class="tag" data-tab="about" @click="changeTab" :class="currentTag=='about' ? 'on' : ''">{{$t('m.aboutme')}}</span>
        <span class="tag" data-tab="sport" @click="changeTab" :class="currentTag=='sport' ? 'on' : ''">{{$t('m.sport_results')}}</span>
        <span class="tag" data-tab="contact" @click="changeTab" :class="currentTag=='contact' ? 'on' : ''">{{$t('m.contact')}}</span>
    </nav>
</template>
<style>

.cate .tag{
    background-color:#f4f4f4;
    color: #4a4a4a;
    height: 2em;
    line-height:100%;
    padding:.5em 1em;
    margin:5px;
    font-size: 14px;
    border-radius: 20px;
    cursor: pointer;
}
.cate .tag.on{
  background-color:#d6dbdb;
}
</style>

<script>
import {getSetting} from '../api/index'
export default {
  name: 'About',
  data(){
      return {
          setting:{},
          currentTag:"about"
      }
  },
  methods:{
    fetchDetail(){
        getSetting((res)=>{
            if(res.length>0){
                this.setting=res[0];
            }
        })
    },
    changeTab(e){
        let tab=e.currentTarget.dataset.tab
        this.currentTag=tab
        this.$emit('loadAbout', tab)
    }
  },
    mounted(){
        this.fetchDetail();
    }
}
</script>


