<template>
<section>
  <Nav></Nav>
  <div class="container section">
    <div class="columns is-multiline" v-if="articles.length>0">
      <div class="column is-12-mobile is-6-tablet is-4-widescreen is-6-desktop" :key="item.id" v-for="item in articles">
        <div class="item post-card bottom-border">
          <router-link class="item-link" :to="{path: `/blog/${item.objectId}`}">
            <figure class="image lazyload is-2by1 item-figure background-img initial loaded" :style="{backgroundImage:'url(' + item.thumb + ')'}"></figure>
          </router-link>
          <!-- <div class="item-tags">
            <a href="" class="button tag-entertainment is-rounded">#Gaming</a>
          </div> -->
          <h4 class="title item-title is-size-4 has-text-weight-extra-bold">
            <router-link class="item-link" :to="{path: `/blog/${item.objectId}`}">{{item.title}}</router-link>
          </h4>
          <div class="item-description">{{item.content}}</div>
          <div class="level">
            <div class="level-left">
              <div class="item-author">{{item.author}}</div>
            </div>
            <div class="level-right">
              <time>{{item.createdAt}}</time>
            </div>
          </div>
        </div>
      </div>
      <nav class="pagination is-rounded" role="navigation" aria-label="pagination">
          <a class="pagination-previous" title="This is the first page" v-bind:disabled="page==1">Previous</a>
          <a class="pagination-next" v-bind:disabled="page==pageCount">Next page</a>
          <ul class="pagination-list">
              <li v-for="num in pageCount" v-bind:key="num">
                  <router-link class="pagination-link" :class="{'is-current' : page==num}" :to="{path: `/blog/?page=${num}`}" @click="changePage(num)">{{num}}</router-link>                    
              </li>
          </ul>
      </nav>
    </div>  
    <div class="container section loading" v-else><span class="_loader"></span></div>   
  </div>
  <Footer></Footer>
</section>
 
</template>
<script>
  import Nav from '../../components/nav'
  import Footer from '../../components/footer'
  import About from '../../components/about'
  import {getBlogListByPage} from '../../api/index'
  export default {
    data() {
      return {          
          page:1,
          pageSize:10,
          pageCount:1,
          totalSize: 0,
          articles: []
      }
    },
    watch: {
    },
    methods: {
        fetchList(){
            this.page=this.$route.query.page || 1;
            getBlogListByPage(this.page, this.pageSize, res=>{
                this.articles= res;
                this.totalSize= res.length;
                this.pageCount= Math.floor(this.totalSize/this.pageSize)+1;
            })
        }
    },
    components: {
      Nav,
      About,
      Footer
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



